import * as yup from "yup";

export const CreatePayoutValidation = yup.object().shape({
    mode: yup
        .string()
        .required("Please choose account type.")
        .oneOf(["bank_account", "vpa"]),
    amount: yup.number().required("Please enter amount").min(1, 'Amount should be greater than 1').max(yup.ref('availableBalance'), 'Amount should not be greater than available balance')
});